import { AfterViewInit, Component, OnInit } from '@angular/core';
import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';
import { Observable } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';
import { eMenuAdmin, environment } from 'src/environments/environment';
import { ActivatedRoute, Router } from '@angular/router';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'app-main-nav',
  templateUrl: './main-nav.component.html',
  styleUrls: ['./main-nav.component.scss'],
})
export class MainAdminComponent implements OnInit {
  __localStorage = localStorage.getItem(environment.key_storage.MENU.ADMIN);
  __eMenu = eMenuAdmin;
  _title = 'ĐHBK - Quản lý Phòng thí nghiệm';
  _displayname = 'Menu';
  isHandset$: Observable<boolean> = this.breakpointObserver
    .observe(Breakpoints.Handset)
    .pipe(
      map((result) => result.matches),
      shareReplay()
    );

  isAdmin = false;
  constructor(
    private breakpointObserver: BreakpointObserver,
    private router: Router,
    private _route: ActivatedRoute
  ) {}
  ngOnInit() {
    let roleId = localStorage.getItem(environment.key_storage.ROLE_ID.ADMIN);
    let token = localStorage.getItem(environment.key_storage.TOKEN.ADMIN);
    if (!token && roleId != environment.roleIdSinhVien) {
      this.showLogin();
    } else {
      this._displayname = localStorage.getItem(
        environment.key_storage.DISPLAY_NAME.ADMIN
      );
      if (this._displayname.length > 17) {
        this._displayname = this._displayname.slice(0, 16) + '...';
      }
      this.isAdmin =
        roleId != environment.roleIdSinhVien &&
        roleId != environment.roleIdGiangVien;

    }
  }
  showLogin() {
    this.router.navigate(['../login'], { relativeTo: this._route });
  }
  logout() {
    this.showLogin();
  }
}
